import React, { useState, useEffect } from "react";
import {
  Button,
  Card,
  CircularProgress,
  Typography,
  Dialog,
  DialogContent,
  TextField,
} from "@mui/material";
import { toast } from "react-toastify";
import { checkVerifyRemitter } from "../../api/login/Login";
import { usePpiBankTransfer } from "./BankTransferPPIProvider";
import PpiPageLayout from "./PpiPageLayout";
import "./PpiTransactionReport.css";

const PpiStatusPage = () => {
  const { setGlobalLoader, setCurrentPage, setBeneficiaryList, globalLoader } =
    usePpiBankTransfer();

  const [mobile, setMobile] = useState("");
  const [otp, setOtp] = useState("");
  const [openOtpPopup, setOpenOtpPopup] = useState(false);
  const [sendOtpLoading, setSendOtpLoading] = useState(false);
  const [verifyOtpLoading, setVerifyOtpLoading] = useState(false);



  // Auto-fill mobile if already saved
  useEffect(() => {
    const savedMobile = localStorage.getItem("mobile")?.slice(-10);
    if (savedMobile) setMobile(savedMobile);
  }, []);

  const handleProceed = async () => {
    if (sendOtpLoading) return;

    if (mobile.length !== 10) {
      return toast.error("Enter valid 10 digit mobile number");
    }

    localStorage.setItem("mobile", mobile);
    setSendOtpLoading(true);
    setGlobalLoader(true);

    try {
      const res = await checkVerifyRemitter({ mobile });
      const apiData = res?.data?.data;

      if (apiData?.statuscode === "OTP") {
        toast.success("OTP Sent Successfully");
        setOpenOtpPopup(true);
      } else if (apiData?.statuscode === "ERR") {
        toast.error(apiData?.status || "OTP request failed");
      } else {
        toast.error("Unexpected response from server");
      }
    } catch (error) {
      toast.error(
        error?.response?.data?.message ||
        "Failed to send OTP. Please try again.",
      );
    } finally {
      setSendOtpLoading(false);
      setGlobalLoader(false);
    }

  };
  const otpInputRef = React.useRef(null);

  // VERIFY OTP
  const handleVerifyOtp = async () => {
    if (verifyOtpLoading) return;
    if (!otp) return toast.error("Enter OTP");

    setVerifyOtpLoading(true);
    setGlobalLoader(true);

    try {
      const res = await checkVerifyRemitter({ mobile, otp });
      const responseBody = res?.data?.data;
      const statusCode = responseBody?.statuscode;

      if (statusCode === "TXN") {
        toast.success("OTP Verified!");
        setBeneficiaryList(responseBody?.data);
        setOpenOtpPopup(false);
        setCurrentPage("ppi-send-money");
      } else if (statusCode === "RNF") {
        toast.info("Remitter Not Found. Please Register.");
        setOpenOtpPopup(false);
        setCurrentPage("ppi-add-customer");
      } else {
        toast.error(responseBody?.status || "Verification Failed");
      }
    } catch {
      toast.error("Invalid OTP");
    } finally {
      setVerifyOtpLoading(false);
      setGlobalLoader(false);
    }

  };

  return (
    <PpiPageLayout title="PPI Bank Transfer">
      <Card
        sx={{
          p: 3,
          borderRadius: "14px",
          backgroundColor: "var(--bs-primary1)",
          color: "var(--bs-primary2)",
          textAlign: "center",
          maxWidth: "360px",
          margin: "0 auto",
          mt: 4,
          boxShadow: "0 2px 10px rgba(0,0,0,0.2)",
        }}
      >
        <Typography
          fontSize="20px"
          fontWeight={700}
          sx={{ color: "#193968", mb: 1 }}
        >
          Verify your identity to continue
        </Typography>
        <Typography fontSize="14px" sx={{ color: "#5a7191", mb: 3 }}>
          Enter your registered mobile number to continue.
        </Typography>

        <TextField
          fullWidth
          label="Enter Mobile Number"
          value={mobile}
          onChange={(e) => setMobile(e.target.value.replace(/\D/g, ""))}
          inputProps={{ maxLength: 10 }}
          onKeyDown={(e) => {
            if (e.key === "Enter") {
              e.preventDefault();
              if (!sendOtpLoading) handleProceed();
            }
          }}
          className="mui-autofill-fix"
          sx={{
            mb: 3,
            "& .MuiOutlinedInput-root": {
              borderRadius: "14px",
              backgroundColor: "#deecff",
            },
            "& .MuiOutlinedInput-notchedOutline": {
              borderColor: "#90a7c4",
            },
            "&:hover .MuiOutlinedInput-notchedOutline": {
              borderColor: "#193968",
            },
            "&.Mui-focused .MuiOutlinedInput-notchedOutline": {
              borderColor: "#193968",
            },
          }}
        />

        <Button
          onClick={handleProceed}
          type="button"
          variant="contained"
          disabled={sendOtpLoading}
          sx={{
            py: 1.4,
            borderRadius: "50px",
            fontWeight: 700,
            backgroundColor: "#193968",
            color: "#fff",
            "&:hover": {
              backgroundColor: "#152c56",
            },
            "&.Mui-disabled": {
              backgroundColor: "#d5d8dd",
              color: "#9aa4af",
            },
          }}
        >
          {sendOtpLoading ? (
            <CircularProgress size={22} sx={{ color: "#fff" }} />
          ) : (
            "Send OTP"
          )}
        </Button>
      </Card>

      {/* OTP POPUP */}
      <Dialog
        open={openOtpPopup}
        disableRestoreFocus
        onClose={() => {
          setOpenOtpPopup(false);
          setOtp("");
        }}

        fullWidth
        maxWidth="xs"
        TransitionProps={{
          onEntered: () => {
            otpInputRef.current?.focus();
          },
        }}
        PaperProps={{
          sx: {
            borderRadius: "20px",
            background: "linear-gradient(145deg,#ffffff,#f5f7fc)",
            p: 1,
          },
        }}
      >
        <DialogContent
          sx={{ p: 3 }}
        >
          <Typography
            fontSize="18px"
            fontWeight={700}
            textAlign="center"
            sx={{ color: "#193968", mb: 2 }}
          >
            Enter OTP
          </Typography>

          <TextField
            fullWidth
            inputRef={otpInputRef}
            className="mui-autofill-fix"
            label="6 Digit OTP"
            value={otp}
            inputProps={{
              maxLength: 6,
              style: {
                letterSpacing: "4px",
                textAlign: "center",
                fontSize: "18px",
                fontWeight: 600,
              },
            }}
            onChange={(e) => setOtp(e.target.value.replace(/\D/g, ""))}
            onKeyDown={(e) => {
              if (e.key === "Enter" && !verifyOtpLoading) {
                e.preventDefault();
                handleVerifyOtp();
              }

            }}
            sx={{
              "& .MuiOutlinedInput-root": {
                borderRadius: "14px",
                backgroundColor: "#deecff",
              },
            }}
          />

          <Button
            fullWidth
            type="button"
            disabled={verifyOtpLoading}
            onClick={handleVerifyOtp}
            sx={{
              mt: 3,
              py: 1.4,
              borderRadius: "50px",
              fontWeight: 700,
              backgroundColor: "#193968",
              color: "#fff",
              "&:hover": {
                backgroundColor: "#152c56",
              },
            }}
          >
            {verifyOtpLoading ? (
              <CircularProgress size={22} sx={{ color: "#fff" }} />
            ) : (
              "Verify OTP"
            )}
          </Button>
        </DialogContent>
      </Dialog>
    </PpiPageLayout>
  );
};

export default PpiStatusPage;
