import React, { useState, useEffect } from "react";
import {
  Box,
  Card,
  Typography,
  Button,
  Dialog,
  DialogContent,
  TextField,
  MenuItem,
  Grid,
  Divider,
  useMediaQuery,
} from "@mui/material";
import { toast } from "react-toastify";
import { usePpiBankTransfer } from "./BankTransferPPIProvider";
import {
  generateTransactionOtp,
  ppiTransaction,
  deletePpiBeneficiary,
  deletePpiBeneficiaryVerify,
} from "../../api/login/Login";
import PpiPageLayout from "./PpiPageLayout";

const PpiSendMoney = () => {
  const { beneficiaryList, setCurrentPage } = usePpiBankTransfer();
  const remitter = beneficiaryList;
  const beneficiaries = remitter?.beneficiaries || [];

  const [openPopup, setOpenPopup] = useState(false);
  const [openDeletePopup, setOpenDeletePopup] = useState(false);
  const [selectedBen, setSelectedBen] = useState(null);
  const [showOtpField, setShowOtpField] = useState(false);
  const isMobile = useMediaQuery("(max-width:600px)");

  const [location, setLocation] = useState({ latitude: "0", longitude: "0" });
  const [form, setForm] = useState({
    transferMode: "",
    transferAmount: "",
    otp: "",
  });
  const [mobile, setMobile] = useState("");
  const [deleteOtp, setDeleteOtp] = useState("");
  const [btnLoading, setBtnLoading] = useState(false);

  useEffect(() => {
    const savedMobile = localStorage.getItem("mobile")?.slice(-10);
    if (savedMobile) setMobile(savedMobile);
  }, []);

  useEffect(() => {
    navigator.geolocation?.getCurrentPosition(
      (pos) =>
        setLocation({
          latitude: pos.coords.latitude,
          longitude: pos.coords.longitude,
        }),
      () => toast.warning("Location Disabled, Using Default"),
    );
  }, []);

  const handleOpenPopup = (ben) => {
    setSelectedBen(ben);
    setOpenPopup(true);
    setShowOtpField(false);
    setForm({ transferMode: "", transferAmount: "", otp: "" });
  };

  const handleSendOtp = async () => {
    if (btnLoading) return;

    if (!form.transferMode || !form.transferAmount)
      return toast.error("Enter Transfer Mode & Amount");

    if (mobile.length !== 10) return toast.error("Invalid mobile number");

    setBtnLoading(true);

    try {
      const res = await generateTransactionOtp({
        beneficiaryId: selectedBen.id,
        beneficiary_name: selectedBen.name,
        bank_name: selectedBen.bank,
        accountNumber: selectedBen.account,
        ifsc: selectedBen.ifsc,
        transferMode: form.transferMode,
        amount: form.transferAmount,
        mobile,
        latitude: location.latitude,
        longitude: location.longitude,
      });

      const statusCode = res?.data?.data?.statuscode;

      if (statusCode === "OTP") {
        toast.success("OTP Sent Successfully");
        setShowOtpField(true);
      } else if (statusCode === "IST") {
        toast.error(res?.data?.data?.status || "Session expired");
        localStorage.removeItem("mobile");
        setCurrentPage("ppi-status-page");
      } else {
        toast.error(res?.data?.data?.status || "Failed to send OTP");
      }
    } catch {
      toast.error("Something went wrong");
    } finally {
      setBtnLoading(false);
    }
  };

  const handleConfirmPayment = async () => {
    if (btnLoading) return;
    if (!form.otp) return toast.error("Enter OTP");

    setBtnLoading(true);

    try {
      const res = await ppiTransaction({
        beneficiaryId: selectedBen.id,
        beneficiary_name: selectedBen.name,
        bank_name: selectedBen.bank,
        accountNumber: selectedBen.account,
        ifsc: selectedBen.ifsc,
        transferMode: form.transferMode,
        amount: form.transferAmount,
        otp: form.otp,
        mobile,
        latitude: location.latitude,
        longitude: location.longitude,
      });

      const api = res?.data;

      if (api?.status === true) {
        toast.success(api?.message || "Transaction Success");
        setOpenPopup(false);
      } else {
        toast.error(api?.message || "Transaction Failed");
      }
    } catch (error) {
      toast.error(error?.response?.data?.message || "Something went wrong");
    } finally {
      setBtnLoading(false);
    }
  };

  const handleDeleteBeneficiary = async (ben) => {
    if (btnLoading) return;

    setSelectedBen(ben);
    setBtnLoading(true);

    try {
      const res = await deletePpiBeneficiary({
        beneficiaryId: ben?.id,
        mobile,
      });

      const apiData = res?.data?.data;
      const statusCode = apiData?.statuscode;

      if (statusCode === "OTP") {
        toast.success("OTP Sent for Delete Verification");
        setOpenDeletePopup(true);
      } else {
        toast.error(apiData?.status || "Failed to send delete OTP");
      }
    } catch {
      toast.error("Delete request failed");
    } finally {
      setBtnLoading(false);
    }
  };

  const handleDeleteBeneficiaryVerify = async () => {
    if (btnLoading) return;
    if (!deleteOtp) return toast.error("Enter OTP");

    setBtnLoading(true);

    try {
      const res = await deletePpiBeneficiaryVerify({
        beneficiaryId: selectedBen?.id,
        otp: deleteOtp,
        mobile,
      });

      const api = res?.data?.data;
      const statusCode = api?.statuscode;

      if (statusCode === "TXN") {
        toast.success("Beneficiary Removed Successfully");
        setDeleteOtp("");
        setOpenDeletePopup(false);
        setCurrentPage("ppi-status-page");
      } else if (statusCode === "IST") {
        toast.error(api?.status || "Invalid or expired session token");
        localStorage.removeItem("mobile");
        setDeleteOtp("");
        setOpenDeletePopup(false);
        setCurrentPage("ppi-status-page");
      } else {
        toast.error(api?.status || "Delete failed");
      }
    } catch {
      toast.error("Invalid OTP");
    } finally {
      setBtnLoading(false);
    }
  };
  const inputSx = {
    mb: 2,
    "& .MuiOutlinedInput-root": {
      borderRadius: "14px",
      backgroundColor: "#deecff",
    },
  };

  return (
    <PpiPageLayout title="Send Money">
      <Card
        sx={{
          p: { xs: 2.5, sm: 3 },
          mb: 3,
          borderRadius: "20px",
          background: "linear-gradient(135deg,#ffffff,#f8f9fb)",
          boxShadow: "0 6px 20px rgba(0,0,0,0.06)",
        }}
      >
        <Typography fontWeight={700} fontSize={{ xs: 18, sm: 20 }}>
          Remitter Details
        </Typography>

        <Divider sx={{ my: 1, borderColor: "#e5e9f2" }} />

        <Grid container spacing={2}>
          <Grid item xs={12} sm={6}>
            <Typography fontSize={{ xs: 14, sm: 15 }}>
              <b>Name:</b> {remitter?.firstName} {remitter?.lastName}
            </Typography>
            <Typography fontSize={{ xs: 14, sm: 15 }}>
              <b>Mobile:</b> {mobile}
            </Typography>
          </Grid>
          <Grid item xs={12} sm={6}>
            <Typography fontSize={{ xs: 14, sm: 15 }}>
              <b>Total Limit:</b> {remitter?.limitTotal}
            </Typography>
            <Typography fontSize={{ xs: 14, sm: 15 }}>
              <b>Available:</b> {remitter?.limitAvailable}
            </Typography>
            <Typography fontSize={{ xs: 14, sm: 15 }}>
              <b>Used:</b> {remitter?.limitConsumed}
            </Typography>
          </Grid>
        </Grid>
      </Card>
      <Box display="flex" justifyContent="flex-end" mb={2}>
        <Button
          variant="contained"
          onClick={() => setCurrentPage("ppi-add-beneficiary")}
          sx={{
            borderRadius: "50px",
            fontWeight: 700,
            px: 4,
            py: 1.2,
            backgroundColor: "#193968",
            color: "#fff",
            "&:hover": { backgroundColor: "#152c56" },
          }}
        >
          + Add Beneficiary
        </Button>
      </Box>

      <Grid container spacing={2}>
        {beneficiaries
          ?.sort((a, b) => {
            if (
              a.bankStatus === "REGISTERED" &&
              b.bankStatus === "UNREGISTERED"
            )
              return -1;
            if (
              a.bankStatus === "UNREGISTERED" &&
              b.bankStatus === "REGISTERED"
            )
              return 1;
            return 0;
          })
          .map((ben) => (
            <Grid key={ben.id} item xs={12} sm={6} md={4} lg={3} xl={2}>
              <Card
                sx={{
                  p: 3,
                  borderRadius: "20px",
                  background:
                    ben.bankStatus === "UNREGISTERED"
                      ? "#f1f3f6"
                      : "linear-gradient(135deg,#ffffff,#f8f9fb)",
                  color: "#193968",
                  position: "relative",
                  boxShadow: "0 6px 18px rgba(0,0,0,0.06)",
                  transition: "0.25s",
                  "&:hover":
                    ben.bankStatus === "REGISTERED"
                      ? { transform: "translateY(-6px)" }
                      : {},
                }}
              >
                {ben.bankStatus === "UNREGISTERED" && (
                  <Typography
                    sx={{
                      position: "absolute",
                      top: 12,
                      right: 12,
                      background: "#cfd6df",
                      color: "#5a7191",
                      fontSize: "11px",
                      fontWeight: 700,
                      px: 1.2,
                      borderRadius: "8px",
                    }}
                  >
                    Deleted
                  </Typography>
                )}

                <Typography fontWeight={700} fontSize={{ xs: 17, sm: 18 }}>
                  {ben.name}
                </Typography>
                <Typography fontSize={15}>Account: {ben.account}</Typography>
                <Typography fontSize={15}>IFSC: {ben.ifsc}</Typography>
                <Typography fontSize={15}>Bank: {ben.bank}</Typography>

                <Box
                  display="flex"
                  justifyContent="space-between"
                  gap={1}
                  mt={2}
                >
                  {/* SEND MONEY BUTTON */}
                  <Button
                    fullWidth
                    variant="contained"
                    disabled={ben.bankStatus === "UNREGISTERED"}
                    onClick={() =>
                      ben.bankStatus === "REGISTERED" && handleOpenPopup(ben)
                    }
                    sx={{
                      borderRadius: "30px",
                      fontWeight: 600,
                      backgroundColor:
                        ben.bankStatus === "UNREGISTERED"
                          ? "#d5d8dd"
                          : "#193968",
                      color: "#fff",
                    }}
                  >
                    Send Money
                  </Button>

                  {/* DELETE BUTTON */}
                  <Button
                    fullWidth
                    variant="outlined"
                    disabled={ben.bankStatus === "UNREGISTERED" || btnLoading}
                    onClick={() =>
                      ben.bankStatus === "REGISTERED" &&
                      handleDeleteBeneficiary(ben)
                    }
                    sx={{
                      borderRadius: "30px",
                      fontWeight: 600,
                      borderColor: "#cc9840",
                      color: "#cc9840",
                      "&:hover": {
                        backgroundColor: "#fff6e5",
                      },
                    }}
                  >
                    Delete
                  </Button>
                </Box>
              </Card>
            </Grid>
          ))}
      </Grid>

      {/* SEND MONEY POPUP */}
      <Dialog
        open={openPopup}
        onClose={() => !showOtpField && setOpenPopup(false)}
        fullWidth
        maxWidth="xs"
        fullScreen={isMobile}
        PaperProps={{
          sx: {
            borderRadius: "22px",
            background: "linear-gradient(145deg,#ffffff,#f5f7fc)",
          },
        }}
      >
        <DialogContent
          sx={{
            p: { xs: 2.5, sm: 4 },
          }}
        >
          <Typography
            fontWeight={700}
            textAlign="center"
            fontSize={{ xs: 18, sm: 20 }}
          >
            Send Money
          </Typography>

          <Typography fontWeight={500} mb={2} fontSize="14px" mt={2}>
            Verify your identity to continue
          </Typography>

          {/* BENEFICIARY NAME */}
          <TextField
            fullWidth
            label="Beneficiary Name"
            value={selectedBen?.name || ""}
            disabled
            sx={{ mb: 2 }}
          />

          {/* BANK NAME */}
          <TextField
            fullWidth
            label="Bank Name"
            value={selectedBen?.bank || ""}
            disabled
            sx={{ mb: 2 }}
          />

          {/* MOBILE */}
          <TextField
            fullWidth
            label="Mobile"
            value={mobile}
            disabled
            sx={{ mb: 2 }}
          />

          {/* ACCOUNT */}
          <TextField
            fullWidth
            label="Account Number"
            value={selectedBen?.account || ""}
            disabled
            sx={{ mb: 2 }}
          />

          {/* IFSC */}
          <TextField
            fullWidth
            label="IFSC"
            value={selectedBen?.ifsc || ""}
            disabled
            sx={{ mb: 2 }}
          />

          {/* TRANSFER MODE */}
          <TextField
            select
            fullWidth
            label="Transfer Mode"
            value={form.transferMode}
            disabled={showOtpField}
            sx={inputSx}
            onChange={(e) => setForm({ ...form, transferMode: e.target.value })}
          >
            <MenuItem value="IMPS">IMPS</MenuItem>
            <MenuItem value="NEFT">NEFT</MenuItem>
          </TextField>

          {/* AMOUNT */}
          <TextField
            fullWidth
            label="Amount"
            className="mui-autofill-fix"
            value={form.transferAmount}
            disabled={showOtpField}
            sx={inputSx}
            onChange={(e) =>
              setForm({
                ...form,
                transferAmount: e.target.value.replace(/\D/g, ""),
              })
            }
          />

          {/* OTP */}
          {showOtpField && (
            <TextField
              fullWidth
              className="mui-autofill-fix"
              label="Enter OTP"
              value={form.otp}
              sx={inputSx}
              onChange={(e) => setForm({ ...form, otp: e.target.value })}
            />
          )}

          {!showOtpField ? (
            <Button
              fullWidth
              variant="contained"
              disabled={btnLoading}
              onClick={handleSendOtp}
              sx={{
                mt: 2,
                py: 1.4,
                borderRadius: "50px",
                fontWeight: 700,
                backgroundColor: "#193968",
                color: "#fff",
                "&:hover": { backgroundColor: "#152c56" },
              }}
            >
              {btnLoading ? "Sending OTP..." : "Send OTP"}
            </Button>
          ) : (
            <Button
              fullWidth
              variant="contained"
              disabled={btnLoading}
              onClick={handleConfirmPayment}
              sx={{
                mt: 2,
                py: 1.4,
                borderRadius: "50px",
                fontWeight: 700,
                backgroundColor: "#193968",
                color: "#fff",
              }}
            >
              {btnLoading ? "Processing..." : "Confirm Payment"}
            </Button>
          )}

          <Button
            fullWidth
            sx={{
              mt: 2,
              borderRadius: "50px",
              color: "#193968",
              fontWeight: 600,
            }}
            onClick={() => setOpenPopup(false)}
          >
            Cancel
          </Button>
        </DialogContent>
      </Dialog>

      {/* DELETE OTP POPUP */}
      <Dialog
        open={openDeletePopup}
        onClose={() => setOpenDeletePopup(false)}
        fullWidth
        maxWidth="xs"
      >
        <DialogContent
          sx={{
            p: { xs: 2.5, sm: 4 },
          }}
        >
          <Typography
            fontWeight={700}
            textAlign="center"
            fontSize={{ xs: 18, sm: 20 }}
            mb={2}
          >
            Enter OTP
          </Typography>

          <TextField
            fullWidth
            className="mui-autofill-fix"
            label="OTP"
            value={deleteOtp}
            inputProps={{ maxLength: 6 }}
            onChange={(e) => setDeleteOtp(e.target.value.replace(/\D/g, ""))}
            sx={inputSx}
          />

          <Button
            fullWidth
            variant="contained"
            disabled={btnLoading}
            onClick={handleDeleteBeneficiaryVerify}
            sx={{
              mt: 3,
              borderRadius: "50px",
              backgroundColor: "#cc9840",
              color: "#fff",
              fontWeight: 700,
            }}
          >
            {btnLoading ? "Deleting..." : "Confirm Delete"}
          </Button>

          <Button
            fullWidth
            sx={{
              mt: 2,
              borderRadius: "50px",
              color: "#193968",
              fontWeight: 600,
            }}
            onClick={() => {
              setDeleteOtp("");
              setOpenDeletePopup(false);
            }}
          >
            Cancel
          </Button>
        </DialogContent>
      </Dialog>
    </PpiPageLayout>
  );
};

export default PpiSendMoney;
