import React, { useEffect, useState } from "react";
import {
  Button,
  Card,
  TextField,
  Dialog,
  DialogContent,
  Typography,
} from "@mui/material";

import Autocomplete from "@mui/material/Autocomplete";
import { toast } from "react-toastify";

import { usePpiBankTransfer } from "./BankTransferPPIProvider";
import {
  getPpiBankList,
  addPpiBeneficiary,
  verifyPpiBeneficiary,
} from "../../api/login/Login";
import PpiPageLayout from "./PpiPageLayout";

const PpiAddBeneficiary = () => {
  const { setCurrentPage, setGlobalLoader } = usePpiBankTransfer();
  const [btnLoading, setBtnLoading] = useState(false);

  const [bankList, setBankList] = useState([]);
  const [mobile, setMobile] = useState("");
  const [showOtpModal, setShowOtpModal] = useState(false);
  const [location, setLocation] = useState({
    latitude: "",
    longitude: "",
  });

  const [otp, setOtp] = useState("");

  useEffect(() => {
    const savedMobile = localStorage.getItem("mobile")?.slice(-10);
    if (savedMobile) setMobile(savedMobile);
  }, []);

  useEffect(() => {
    if (!navigator.geolocation) {
      toast.error("Geolocation is not supported by this browser");
      return;
    }

    navigator.geolocation.getCurrentPosition(
      (position) => {
        setLocation({
          latitude: position.coords.latitude,
          longitude: position.coords.longitude,
        });
      },
      (error) => {
        toast.error("Please allow location access");
        console.error(error);
      },
      {
        enableHighAccuracy: true,
        timeout: 10000,
        maximumAge: 0,
      },
    );
  }, []);

  const [form, setForm] = useState({
    name: "",
    accountNumber: "",
    ifsc: "",
    bankId: "",
  });

  const fetchBankList = async () => {
    try {
      const res = await getPpiBankList();
      setBankList(res?.data?.data || []);
    } catch {
      toast.error("Bank List Load Failed");
    }
  };

  useEffect(() => {
    fetchBankList();
  }, []);

  const handleSubmit = async () => {
    if (!form.name || !form.accountNumber || !form.ifsc || !form.bankId) {
      toast.error("All fields are required");
      return;
    }

    const payload = {
      name: form.name,
      accountNumber: form.accountNumber,
      ifsc: form.ifsc,
      bankId: form.bankId,
      mobile,
    };

    setBtnLoading(true); //  ADD
    setGlobalLoader(true);

    try {
      const res = await addPpiBeneficiary(payload);
      const data = res?.data?.data;

      if (data?.statuscode === "OTP") {
        toast.success("OTP sent to registered mobile");
        setShowOtpModal(true);
      } else if (data?.statuscode === "TXN") {
        toast.success("Beneficiary Added Successfully!");
        setCurrentPage("ppi-status-page");
      } else {
        toast.error(data?.status || "Failed to add beneficiary");
      }
    } catch (err) {
      toast.error("Failed to add beneficiary");
    } finally {
      setBtnLoading(false); //  ADD
      setGlobalLoader(false);
    }
  };

  const handleVerifyOtp = async () => {
    if (!otp || otp.length !== 6) {
      toast.error("Enter valid 6 digit OTP");
      return;
    }

    setBtnLoading(true); //  ADD
    setGlobalLoader(true);

    try {
      const res = await verifyPpiBeneficiary({
        mobile,
        otp,
        latitude: location.latitude,
        longitude: location.longitude,
      });

      const data = res?.data?.data;

      if (data?.statuscode === "TXN") {
        toast.success("Beneficiary Verified Successfully");
        setShowOtpModal(false);
        setOtp("");
        setCurrentPage("ppi-status-page");
      } else {
        toast.error(data?.status || "OTP verification failed");
      }
    } catch {
      toast.error("OTP verification failed");
    } finally {
      setBtnLoading(false); //  ADD
      setGlobalLoader(false);
    }
  };
  const inputSx = {
    mb: 2,
    "& .MuiOutlinedInput-root": {
      borderRadius: "14px",
      backgroundColor: "#deecff",
    },
    "& .MuiOutlinedInput-notchedOutline": {
      borderColor: "#90a7c4",
    },
    "&:hover .MuiOutlinedInput-notchedOutline": {
      borderColor: "#193968",
    },
    "&.Mui-focused .MuiOutlinedInput-notchedOutline": {
      borderColor: "#193968",
    },
  };

  return (
    <PpiPageLayout title="Add Beneficiary">
      <div
        style={{
          minHeight: "calc(100vh - 64px)",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <Card
          sx={{
            p: { xs: 2.5, sm: 4 },
            borderRadius: "24px",
            background: "linear-gradient(135deg,#ffffff,#f8f9fb)",
            boxShadow: "0 6px 22px rgba(0,0,0,0.06)",
            maxWidth: 420,
            width: "100%",
          }}
        >
          <TextField
            fullWidth
            label="Registered Mobile Number"
            value={mobile}
            disabled
            sx={{ mb: 2 }}
            className="mui-autofill-fix"
          />

          <TextField
            fullWidth
            label="Beneficiary Name"
            name="name"
            sx={inputSx}
            value={form.name}
            onChange={(e) => setForm({ ...form, name: e.target.value })}
            className="mui-autofill-fix"
          />

          <TextField
            fullWidth
            label="Account Number"
            name="accountNumber"
            sx={inputSx}
            value={form.accountNumber}
            onChange={(e) =>
              setForm({ ...form, accountNumber: e.target.value })
            }
            className="mui-autofill-fix"
          />

          <Autocomplete
            fullWidth
            options={bankList}
            sx={{
              mb: 2,
              "& .MuiOutlinedInput-root": {
                borderRadius: "14px",
              },
              "& .MuiOutlinedInput-notchedOutline": {
                borderColor: "#90a7c4",
              },
              "&:hover .MuiOutlinedInput-notchedOutline": {
                borderColor: "#193968",
              },
              "&.Mui-focused .MuiOutlinedInput-notchedOutline": {
                borderColor: "#193968",
              },
            }}
            getOptionLabel={(option) => option.bank_name}
            onChange={(e, selected) =>
              setForm({
                ...form,
                bankId: selected?.bankID || "",
                ifsc: selected?.ifsc_code || "",
              })
            }
            renderInput={(params) => (
              <TextField
                {...params}
                label="Search Bank"
                className="mui-autofill-fix"
                sx={{
                  mb: 2,
                  background: "#fbfcfd",
                  "& .MuiOutlinedInput-root": {
                    borderRadius: "14px",
                  },
                  "& .MuiOutlinedInput-notchedOutline": {
                    borderColor: "#90a7c4",
                  },
                  "&:hover .MuiOutlinedInput-notchedOutline": {
                    borderColor: "#193968",
                  },
                  "&.Mui-focused .MuiOutlinedInput-notchedOutline": {
                    borderColor: "#193968",
                  },
                }}
              />
            )}
            className="mui-autofill-fix"
          />

          <TextField
            fullWidth
            label="IFSC Code"
            name="ifsc"
            sx={inputSx}
            value={form.ifsc}
            disabled
          />

          <Button
            fullWidth
            variant="contained"
            disabled={btnLoading}
            onClick={handleSubmit}
            sx={{
              mt: 2,
              py: 1.4,
              borderRadius: "50px",
              fontWeight: 700,
              backgroundColor: "#193968",
              color: "#fff",
              "&:hover": { backgroundColor: "#152c56" },
            }}
          >
            {btnLoading ? "Processing..." : "Add Beneficiary"}
          </Button>

          <Button
            fullWidth
            onClick={() => setCurrentPage("ppi-send-money")}
            sx={{
              mt: 1,
              borderRadius: "50px",
              color: "#193968",
              fontWeight: 600,
            }}
          >
            Cancel
          </Button>
        </Card>
      </div>
      <Dialog
        open={showOtpModal}
        onClose={() => {
          setShowOtpModal(false);
          setOtp("");
        }}
        fullWidth
        maxWidth="xs"
        PaperProps={{
          sx: {
            borderRadius: "20px",
            background: "linear-gradient(145deg,#ffffff,#f5f7fc)",
          },
        }}
      >
        <DialogContent sx={{ p: 3 }}>
          <Typography
            fontSize="18px"
            fontWeight={700}
            textAlign="center"
            sx={{ color: "#193968", mb: 2 }}
          >
            Verify OTP
          </Typography>

          <TextField
            fullWidth
            autoFocus
            className="mui-autofill-fix"
            label="6 Digit OTP"
            value={otp}
            inputProps={{
              maxLength: 6,
              style: {
                letterSpacing: "4px",
                textAlign: "center",
                fontSize: "18px",
                fontWeight: 600,
              },
            }}
            onChange={(e) => setOtp(e.target.value.replace(/\D/g, ""))}
            sx={inputSx}
          />

          <Button
            fullWidth
            disabled={btnLoading}
            onClick={handleVerifyOtp}
            sx={{
              mt: 3,
              py: 1.4,
              borderRadius: "50px",
              fontWeight: 700,
              backgroundColor: "#193968",
              color: "#fff",
            }}
          >
            {btnLoading ? "Verifying..." : "Verify OTP"}
          </Button>

          <Button
            fullWidth
            sx={{
              mt: 1.5,
              borderRadius: "50px",
              color: "#193968",
              fontWeight: 600,
            }}
            onClick={() => {
              setShowOtpModal(false);
              setOtp("");
            }}
          >
            Cancel
          </Button>
        </DialogContent>
      </Dialog>
    </PpiPageLayout>
  );
};

export default PpiAddBeneficiary;
